<script>
export default {
  name: "error-page-component",
  data() {
    return {
      maintainError: [],
    };
  },
  methods: {
    goBack() {
      this.$router.replace({ name: "index" });
      // this.$router.go(-1);
    },
  },
  mounted() {
    this.$root.overlay = false;
    this.$store.commit("HEADER_HANDLER", false);
    this.$store.commit("BREADCRUMB_HANDLER", false);
    this.$root.$children[0].sidebarSwitch = false;
    if (this.$route.name === "999") {
      this.maintainError = this.$store.state.maintainData;
    }
  },
  destroyed() {},
};
</script>

<template src="./404.html"></template>
